import React from "react";
import { InputGroup } from "react-bootstrap";
import { HiOutlineMail } from "react-icons/hi";
import { HiLockClosed } from "react-icons/hi2";
import styled from "styled-components";
import useAxios from "../api/Axios";
import { useAuth } from "../Context/Auth";
import userImage from "../components/images/man (1).png";
import { toast } from "react-toastify";
import { useLocation, useNavigate } from "react-router-dom";
import useCookies from "react-cookie/cjs/useCookies";

const TeacherLogin = () => {
  const axios = useAxios();
  const navigate = useNavigate();
  const location = useLocation();
  const auth = useAuth();
  const [loginData, setLoginData] = React.useState();
  const [cookie, setCookie, removeCookie] = useCookies(["log"]);
  const [loading, setLoading] = React.useState(true);

  React.useEffect(() => {
    const reLogin = async () => {
      const controller = new AbortController();
      const signal = controller.signal;
      try {
        setLoading(true);
        const url = "/relogin";
        await axios
          .get(url, { signal })
          .then((res) => {
            const data = res.data.data[0];
            auth.user = {
              id: data.id,
              name: data.teacherName,
              image: data.image,
            };
            if (data.id !== undefined) {
              setCookie("log", res.data.token, { path: "/" });
              const path = location.state?.path || `/t/${data.id}/dashboard`;
              navigate(path, { replace: true });
            } else {
              handleError("Internal Error.");
            }
          })
          .catch((err) => {
            handleError(err.message);
            removeCookie("login");
          });
      } catch (error) {
        handleError(error?.response?.data?.message);
      } finally {
        controller.abort();
        setTimeout(() => {
          setLoading(false);
        }, 1000);
      }
    };
    if (auth.user === null && cookie.log !== undefined) {
      reLogin();
    }
    setTimeout(() => {
      setLoading(false);
    }, 100);
  }, [
    auth,
    axios,
    cookie.log,
    location.state?.path,
    navigate,
    removeCookie,
    setCookie,
  ]);

  const handleSubmit = async (evt) => {
    evt.preventDefault();
    try {
      const url = "/login";
      await axios
        .post(url, loginData)
        .then((res) => {
          const data = res.data.data[0];
          auth.login({
            id: data.id,
            name: data.teacherName,
            image: data.image,
          });
          if (data.id !== undefined) {
            setCookie("log", res.data.token, { path: "/" });
            const path = location.state?.path || `/t/${data.id}/dashboard`;
            navigate(path, { replace: true });
            handleSuccess("Login Successfully.", "success");
          } else {
            handleError("Internal Error.");
          }
        })
        .catch((err) => {
          handleError(err?.response?.data?.message);
          console.log(err);
        });
    } catch (error) {
      handleError(error?.response?.data?.message);
      console.log(error);
    }
  };

  const handleSuccess = (msg) => {
    toast.success(msg, {
      position: "top-right",
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
      theme: "light",
    });
  };

  const handleError = (msg) => {
    toast.error(msg, {
      position: "top-right",
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
      theme: "light",
    });
  };

  return (
    <Section>
      {loading ? (
        "Loading"
      ) : (
        <Form onSubmit={handleSubmit} style={{ paddingBottom: "5rem" }}>
          <FormHeader style={{ marginBottom: "1rem" }}>
            Login Your Account
          </FormHeader>
          <ImageBorder>
            <ImagePanel>
              <Image src={userImage} alt="User Image" />
            </ImagePanel>
          </ImageBorder>
          <InputGroup>
            <Span className="input-group-text" id="Email">
              <HiOutlineMail />
            </Span>
            <Input
              name="email"
              className="form-control"
              type="email"
              placeholder="Email"
              aria-describedby="Email"
              onChange={(e) =>
                setLoginData({ ...loginData, [e.target.name]: e.target.value })
              }
              required
            />
          </InputGroup>
          <InputGroup>
            <Span className="input-group-text" id="Pass">
              <HiLockClosed />
            </Span>
            <Input
              name="pass"
              className="form-control"
              type="password"
              placeholder="Password"
              aria-describedby="Pass"
              onChange={(e) =>
                setLoginData({ ...loginData, [e.target.name]: e.target.value })
              }
              required
            />
          </InputGroup>

          <ButtonGroup>
            <Button style={{ background: "rgba(255, 71, 87,1.0)" }}>
              Cancel
            </Button>
            <Button
              type="submit"
              style={{ background: "rgba(55, 66, 250,1.0)" }}
            >
              Login
            </Button>
          </ButtonGroup>
        </Form>
      )}
    </Section>
  );
};

export default TeacherLogin;

export const Section = styled.section`
  height: calc(100vh - 80px);
  margin-top: 75px;
`;
export const Container = styled.div``;
export const Form = styled.form`
  padding: 1rem;
  width: 400px;
  border: 1px solid rgba(95, 39, 205, 01);
  border-radius: 5px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem 0;

  @media (max-width: 410px) {
    width: 100%;
    border: none;
  }
`;

export const FormHeader = styled.h1`
  @media (min-width: 540px) {
    font-size: 1.3rem;
  }
`;

export const Label = styled.label``;
export const Input = styled.input`
  padding: 0.5rem 1rem;
  border: 1px solid rgba(95, 39, 205, 01);


  &:,
  &:focus {
    outline: none;
    border: 1px solid rgba(95, 39, 205, 0.5);
  }
`;

export const Span = styled.span`
  background: rgba(95, 39, 205, 01);
  color: #fff;
  border: 1px solid rgba(95, 39, 205, 01);
`;

export const ButtonGroup = styled.div`
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin-top: 0.5rem;
  width: 100%;
  @media (max-width: 560px) {
    flex-direction: column-reverse;
    padding: 0;
  }
`;

export const Button = styled.button`
  border: none;
  padding: 0.5rem 1rem;
  font-weight: 600;
  width: 45%;
  color: #fff;
  margin: 1rem 0;

  &:nth-child(1) {
    margin-right: 5px;
  }
  &:nth-child(2) {
    margin-left: 5px;
  }

  @media (max-width: 560px) {
    width: 95%;
    margin: 0.5rem 0;

    &:nth-child(1) {
      margin-right: 0px;
    }
    &:nth-child(2) {
      margin-left: 0px;
    }
  }
`;

const ImageBorder = styled.div`
  width: 155px;
  height: 155px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 10px;
  outline: 3px solid rgba(3, 3, 3, 0.1);
  border-radius: 50%;
`;

const ImagePanel = styled.div`
  width: 150px;
  height: 150px;
  overflow: hidden;
  background: rgba(3, 3, 3, 0.011);
  border-radius: 50%;
`;

const Image = styled.img`
  width: 100%;
  height: 100%;
`;
